import { getRandomInt } from './random';
import '../css/main.scss';

let important = ['vanco-auc calculator', 'ucf coding weekly challenge', 'ucf coding weekly module', 'nap'];
let body = ['beach run', 'lake trail run', 'push ups / squats', 'swim', 'suntan on roof', 'peleton', 'tennis wall'];
let code = ['clash of code', 'team treehouse', 'codeacademy', 'udacity', 'udemy'];
let chores = ['dishes', 'laundry', 'papers', 'groceries'];

let a = [...important, ...body, ...code, ...chores];

for (let i = 6; i < 23; i++) {
  let li = document.createElement('li');
  // li.textContent = a.splice(getRandomInt(0, a.length), 1);
  let prefix = i < 13 ? i : i - 12;
  let ampm = i < 12 ? 'am' : 'pm';
  li.textContent = `${prefix}${ampm}: ${a.splice(getRandomInt(0, a.length - 1), 1)[0]}`;
  document.querySelector('ol').appendChild(li);
}
